.container,
.mainBox,
.topPanel,
.bottomPanel,
.DEBUG {
  /*!KEEP*/
  // border: 1px solid red;
}

.container {
  @include fullSizeBlock;
}

@mixin previewImage {
  background-image: url('./assets/gallery-uffizi.jpg');
  background-size: cover;
  background-position: center;
}

.mainBox {
  @include previewImage;
  position: relative;
}

.previewWindow {
  position: absolute;
  top: 16px;
  right: 16px;
  width: 200px;
  height: 140px;
  border: 2px solid #000;
  background: #333;
  border-radius: 8px;
  box-shadow: 2px 2px 4px color.change(#000, $alpha: 0.7);
  @include previewImage;
}
