@use "sass:math"; @use "sass:color"; @import "css-params-generated.scss"; @import "global-mixins.scss";
/** @module CasterFooterIconButton/module.scss
 *  @since 2023.03.01, 22:00
 *  @changed 2023.03.01, 23:23
 */

.root {
  position: relative;
  // overflow: hidden;
  padding: 2px 12px;
  &.hasIcon {
    padding-left: 8px;
  }
  background: none;
  // @include fullSizeBlock;
  border: 1px solid transparent;
  &,
  .background {
    border-radius: 4px;
  }
  transition: all $transitionTimeMs;
  gap: 4px;
  // Inactive...
  cursor: default;
}

@mixin absoluteFullSize {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-sizing: border-box;
}

.background {
  @include absoluteFullSize;
  .theme-alert & {
    background-color: $errorColor;
  }
}

// Crossed icon
.crossedIcon {
  @include absoluteFullSize;
  transition: opacity $transitionTimeMs;
  opacity: 0;
  // Two unchecked lines...
  &:before,
  &:after {
    position: absolute;
    left: 50%;
    $vSize: -18%;
    top: $vSize;
    bottom: $vSize;
    content: ' ';
    width: 1px;
    background-color: $brightErrorColor;
    transform: rotate(45deg);
  }
  &:after {
    transform: rotate(-45deg);
  }
}

.badge,
.icon,
.text {
  margin: 2px;
  color: #fff;
  position: relative;
}

// Inactive...
.background,
.badge,
.icon,
.text {
  opacity: 0.4;
  // cursor: default;
}

.badge {
  position: relative;
  font-size: 75%;
  background-color: #fff;
  border-radius: 10px;
  color: #000;
  padding: 3px 4px;
  padding-bottom: 0;
  line-height: 100%;
}

.clickable,
.checkable {
  cursor: pointer;
  .background,
  .badge,
  .icon,
  .text {
    opacity: 1;
  }
  &:hover {
    border-color: color.adjust(#fff, $alpha: -0.95);
    background-color: color.adjust(#fff, $alpha: -0.97);
    opacity: 0.8;
  }
}

.unchecked {
  .background,
  .iconSymbol,
  .text {
    opacity: 0.2;
  }
  .crossedIcon {
    opacity: 1;
  }
}
